import React, { useState } from "react";
import "./homepage.css";
import Effect from "./effect/Effect";
import cv from "./asset/cve.pdf";

import { Jumbotron, Container } from "reactstrap";

function Homepage(props) {
  const [state, setState] = useState(false);
  return (
    <Jumbotron id="hero">
      <Container>
        <Effect />
        <div className="bga">
          <h1 className="hero-title">
            HI, MY NAME IS <span className="text-color-main">JIBRIL OSMAN</span>
            <br />
            I'M A FRONTEND-DEVELOPER
          </h1>
          <a color="primary" size="lg" block className="bth" href={cv} download>
            <i className="bi bi-box-arrow-up-right"></i> Download CV
          </a>
        </div>
      </Container>
    </Jumbotron>
  );
}

export default Homepage;
